import React, { Component } from 'react';
import styled from "styled-components";
import LevelItem from './LevelItem.js';
import TriggersTooltips from './ToolTip.js'

const LevelStyle = styled.div `
    width: 100%;
    font-family: 'Noto Sans KR', sans-serif;
    display: flex;
    @media (min-width:0px) and (max-width:680.9px) {
        font-size: 15.9px;
    }
`;

const Red = styled.div`
    color: red;
`

const Green = styled.div`
    color: green;
`

const Blue = styled.div`
    color: blue;
`
const InformationLocation = styled.div`
    margin-top: 3px;
`


const AssistMentStyle = styled.div`
    font-size: 18px;
    font-family: 'Noto Sans KR', sans-serif;
    color: #747474;
    line-height: 30px;
    margin-bottom: 30px;
    @media (min-width:0px) and (max-width: 1023.9px) {
        font-size: 12.5px;
    }
`;

const AssistTitleStyle = styled.div `
    width: 100%;
    height: 100%;
    color: #black;
    font-size: 25px;
    text-decoration: none;
    transition: 1s;
    font-family: 'Do Hyeon', sans-serif;
`;


class LevelBox extends Component {
    level= [{
        text:'■ Lv3',
        fcolor:'red'
        },{
        text:'■ Lv2',
        fcolor:'green'
        },{
        text:'■ Lv1',
        fcolor:'blue'
        }
    ]

    renderLevel = (arr) => {
        return arr.map(a=> {
            return <LevelItem text={a.text} fcolor={a.fcolor}/>
        })
    }

    render() {
        return (
        <div>
            <AssistTitleStyle>
                <p>제 기술을 소개합니다.</p>
            </AssistTitleStyle>
            
            <AssistMentStyle>
                <p>주관적인 기준을 통해 기술의 능력을 단계별로 표현하였습니다.<br />
                기준이 궁금하시다면 하단에 ?를 클릭(터치) 해 주세요.</p>
            </AssistMentStyle>
            <LevelStyle>
            <Red>■ Lv3</Red>&nbsp;&nbsp;&nbsp;<Green>■ Lv2</Green>&nbsp;&nbsp;&nbsp;<Blue>■ Lv1</Blue>
                &nbsp;&nbsp;&nbsp;&nbsp;
                <InformationLocation><TriggersTooltips></TriggersTooltips></InformationLocation>
            </LevelStyle>

            
        </div>
        );
    }
}

export default LevelBox;        