import React, { Component } from 'react';
import styled from "styled-components";

const TitleStyle = styled.div `
    width: 100%;
    margin-bottom: 30px;
    font-size: 25px;    
    color: #127da7;
    font-family: 'PT Sans Narrow', sans-serif;
`;
const HrStyle = styled.div `
    width: 86px;
    margin-top: -30px;
    border: 0;
    height: 3px;
    background-color: black;
`;


class AboutMeTitle extends Component {
    render() {
        return (
        <div>
            <TitleStyle>
                A B O U T &nbsp; M E
            </TitleStyle>
        </div>
        );
    }
}

export default AboutMeTitle;