import React, { Component } from 'react';
import styled, {keyframes} from 'styled-components';
import Tooltip from '@material-ui/core/Tooltip';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Levelinformation from './img/Levelinformation.png'
const Information = styled.div`
    width: 18px;
    height: 18px;
    background-image: url(${Levelinformation});
`

export default function TriggersTooltips() {
  const [open, setOpen] = React.useState(false);

  const handleTooltipClose = () => {
    setOpen(false);
  };

  const handleTooltipOpen = () => {
    setOpen(true);
  };


  return (
    <div>
          <ClickAwayListener onClickAway={handleTooltipClose}>
            <div>
              <Tooltip
                PopperProps={{
                  disablePortal: true,
                }}
                onClose={handleTooltipClose}
                open={open}
                disableFocusListener
                disableHoverListener
                disableTouchListener
                title="LV3 : 프로젝트에 활용이 가능함&nbsp;&nbsp;,&nbsp;&nbsp;LV2 : 프로젝트에 활용이 가능하나 조금 미흡함&nbsp;&nbsp;,&nbsp;&nbsp;LV1 : 복습이 필요함"
                arrow
              >

                <Information onClick={handleTooltipOpen}></Information> 
              </Tooltip>
            </div>
          </ClickAwayListener>
    </div>
  );
}
