import React, { Component } from 'react';
import styled from "styled-components";

const TitleStyle = styled.div`
width: 100%;
margin-bottom: 30px;
font-size: 25px;    
color: #127da7;
font-family: 'PT Sans Narrow', sans-serif;
`;

class SkillTitle extends Component {
    render() {
        return (
        <div>
            <TitleStyle>
                S K I L L S
            </TitleStyle>
        </div>
        );
    }
}

export default SkillTitle;