import React, { Component } from 'react';
import styled from "styled-components";

const AssistTitleStyle = styled.div `
    width: 100%;
    height: 100%;
    color: #black;
    font-size: 25px;
    text-decoration: none;
    transition: 1s;
    font-family: 'Do Hyeon', sans-serif;
`;

const AssistMentStyle = styled.div`
    font-size: 15px;
    font-family: 'Noto Sans KR', sans-serif;
    color: #747474;
    line-height: 30px;

    @media (min-width:0px) and (max-width: 1023.9px) {
        font-size: 12.5px;
    }
`;

class AssistBox extends Component {  
    render() {
        return (
        <div>
            <AssistTitleStyle>
                
                <p>"같이 일하고 싶은 개발자"</p>
            </AssistTitleStyle>
            <AssistMentStyle>
                <p>
                    <b>"같이 일하고 싶은 개발자"</b> 가 되는 것이 꿈인 신준섭입니다.<br />
                    여러 사람들과 함께 업무를 잘 수행하고,<br />
                    회사의 꾸준한 성장을 위해 끝없이 노력하는<br />
                    필요한 사람이 되겠습니다.<br />  
                    
                </p>
            </AssistMentStyle><br></br>
            <AssistMentStyle>
                <p>
                    창의적인 것을 추구하고,<br />
                    새로운 것에 대한 두려움이 없으며,<br />
                    신기술에 대한 지속적인 관심을 갖는<br />
                    저의 이름은
                </p>
            </AssistMentStyle>
        </div>
        )
    }
}

export default AssistBox;