import React, { Component } from 'react';
import styled from "styled-components";
import EditIcon from '@material-ui/icons/Edit';
import orange from '@material-ui/core/colors/orange';

const NoSkillLocation = styled.div`
    height: 100%;
    /*해상도*/

    @media (min-width:0px) and (max-width:680.9px) {
        position: absolute;
        width: 310px;
        height: 100%;
        
        left: 50%;
        margin-left: -155px;
        margin-top: -25px;
    }

    @media (min-width:681px) and (max-width:767.9px) {
        margin-top: -25px;
        margin-left: 100px;
    }
    
    @media (min-width:768px) and (max-width:1023.9px) {
        margin-top: -25px;
        margin-left: 100px;
    }

    @media (min-width: 1024px) and (max-width: 1599.9px) {
        margin-top: -25px;
        margin-left: 307px;
    }
    
    @media (min-width: 1600px) {
        margin-top: -25px;
        margin-left: 20.3%;
    }
`

const MentBoxStyle = styled.div`
    display: flex;
    width: 335px;
    height: 50px;
    background-color: #ffe08c;
    border-radius: 10px 10px 10px 10px;
    box-shadow: 1.5px 1.5px 1px 1px #faecc5;
    @media (min-width:0px) and (max-width:680.9px) {
        width: 310px;
    }
`

const IconStyle = styled.div`
    padding-top: 11.4px;
`

const MentStyle = styled.div`
    padding-top: 15px;
    font-size: 15px;
    font-family: 'Noto Sans KR', sans-serif;
    color: black;

    @media (min-width:0px) and (max-width:680.9px) {
        font-size: 14px;
    }
`


class NoSkill extends Component {
    render() {
        return (
        <div>
            <NoSkillLocation>
                <MentBoxStyle>
                    <IconStyle>
                        &nbsp;&nbsp;&nbsp;<EditIcon style={{ color: orange[900],fontSize: 28}}></EditIcon>
                    </IconStyle>
                    <MentStyle style={{ color: orange[700]}}>
                        &nbsp;&nbsp;타 기술도 빠른 시일 내에 습득 가능합니다!!
                    </MentStyle>
                </MentBoxStyle>
            </NoSkillLocation>
        </div>
        );
    }
}

export default NoSkill;