import React, { Component } from 'react';
import styled from "styled-components";
import ProjectTitle from './ProjectTitle.js'
import Card from './Card.js'

const ProjectStyle = styled.div`
    width: 100%;
    height: 100%;
    margin-top: -30px;
    @media (min-width:0px) and (max-width:680.9px) {
        margin-top: 60px;
    }
`;

const SkillLevelLocation = styled.div `
    margin-top: 40px;
    /*해상도*/
    @media (min-width:0px) and (max-width:680.9px) {
        width: 345px;
        position: absolute;
        left: 50%;
        margin-left: -172.5px;
        margin-top: 90px;
    }

    @media (min-width:681px) and (max-width:767.9px) {
        margin-left: 100px;
    }
    
    @media (min-width:768px) and (max-width:1023.9px) {
        margin-left: 100px;
    }

    @media (min-width: 1024px) and (max-width: 1599.9px) {
        margin-left: 307px;
    }
    
    @media (min-width: 1600px) {
        margin-left: 20.3%;
    }
`;


const Flex = styled.div`
    width: 100%;
    @media (min-width:0px) and (max-width:680px) {
        position: absolute;
        left: 50%;  
        margin-left: -150px;
        overflow: hidden;
    }
    
    @media (min-width:681px) {
        display: flex;
    }
`;
const MarginLeft = styled.div`

    @media (min-width:768px) and (max-width:865.9px) {
        margin-left: 5%;
    }

    @media (min-width: 866px) and (max-width: 1679.9px) {
        margin-left: 3%;
    }

    @media (min-width:1680px) and (max-width:1919.9px) {
        margin-left: 5%;
    }

    @media (min-width:1920px) {
        margin-left: 9.5%;
    }
`

const MarginLeft1 = styled.div`

`


class ProjectBox extends Component {
    
    CardStyle1= [{
        title:<b>dB 기반 위험 방지 APP</b>,
        date: '2020년 04월 23일',
        image: 'https://loremflickr.com/320/240',
        guideMent: '자동차 경보음의 소리를 듣지 못해　　　항상 위험 상황에 노출되어 있는 　　　　청각 장애인들을 위한 　　　　　　　　위험 방지용 모바일 어플이다.',
        chip1: '#Java',
        chip2: '#Google API',
        chip3: '#Android10',
        hiddenMent: '준비중'
    }]

    CardStyle2= [{
        title:<b>OCR 지폐 인식 App</b>,
        date: '2020년 06월 07일',
        image: 'https://picsum.photos/320/240',
        guideMent: '시각장애인들은 지폐에 만들어진 점자를 　식별하기 어렵다고 이야기한다. 　　　　그러한 불편함을 해소하기 위해 개발한　 모바일 어플이다.',
        chip1: '#Java',
        chip2: '#Google API',
        chip3: '#Android10',
        hiddenMent: '준비중'
    }]

    CardStyle3= [{
        title:<b>PortFolio</b>,
        date: '2020년 10월 01일',
        image: 'https://picsum.photos/320/240/?random',
        guideMent: '개발자의 정보 및 프로젝트가 담겨있는　 반응형 웹사이트이다.',
        br: <br></br>,
        chip1: '#React',
        chip2: '#RWD',
        chip3: '#MATERIAL-UI',
        hiddenMent: '준비중'
    }]

    renderCardStyle = (arr) => {
        return arr.map(a=> {
            return <Card title={a.title} date={a.date} image={a.image} guideMent={a.guideMent} chip1={a.chip1} chip2={a.chip2} chip3={a.chip3} br={a.br} hiddenMent={a.hiddenMent} key={a.title}/>
        })
    }

    render() {
        return (
        <div> 
            <ProjectStyle>
            <ProjectTitle></ProjectTitle>
            
                <SkillLevelLocation>
                    
                    <Flex>
                    <MarginLeft1>
                    {this.renderCardStyle(this.CardStyle1)}
                    </MarginLeft1>
                    <br></br>
                    <br></br>
                    <br></br>
                    <MarginLeft>
                    {this.renderCardStyle(this.CardStyle2)}
                    </MarginLeft>
                    <br></br>
                    <br></br>
                    <br></br>
                    <MarginLeft>
                    {this.renderCardStyle(this.CardStyle3)}
                        </MarginLeft>
                    </Flex>
                </SkillLevelLocation>
            </ProjectStyle>
            
        </div>
        );
    }
}

export default ProjectBox;
