import React, { Component } from 'react';
import styled from "styled-components";

const TitleStyle = styled.div`
    width: 100%;
    font-size: 25px;
    color: #127da7;
    font-family: 'PT Sans Narrow', sans-serif;
    margin-top: 60px;
    
`;
const TitleLocation = styled.div`
overflow: hidden;
/*해상도*/
@media (min-width:0px) and (max-width:680.9px) {
    width: 345px;
    position: absolute;
    left: 50%;
    margin-top: -60px;
    margin-left: -150px;
}

@media (min-width:681px) and (max-width:767.9px) {
    margin-left: 100px;
}

@media (min-width:768px) and (max-width:1023.9px) {
    margin-left: 100px;
}

@media (min-width: 1024px) and (max-width: 1599.9px) {
    margin-left: 307px;
}

@media (min-width: 1600px) {
    margin-left: 20.3%;
}`



class ProjectTitle extends Component {
    render() {
        return (
        <div>
            <TitleLocation>
            <TitleStyle>
                P R O J E C T
            </TitleStyle>
            </TitleLocation>
        </div>
        );
    }
}

export default ProjectTitle;